<template>
  <div>
    <!--添加面包屑导航-->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>参数列表</el-breadcrumb-item>
    </el-breadcrumb>
    <!--卡片区域-->
    <el-card>
      <!--警告区域-->
      <el-alert
        title="注意，只允许为第三级分类设置相关参数！"
        type="warning"
        :closable="false"
        show-icon
      >
      </el-alert>
      <!--选择商品分类区域-->
      <el-row class="cat_top">
        <el-col>
          <span>选择商品分类: </span>
          <!--商品分类级联选择框-->
          <el-cascader
            expand-trigger="hover"
            v-model="selectedKeys"
            :options="cateList"
            :props="cateProps"
            @change="cateChange"
          ></el-cascader>
        </el-col>
      </el-row>
      <!--tabs标签页-->
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <!--添加参数面板-->
        <el-tab-pane label="动态参数" name="many">
          <el-button
            type="primary"
            size="mini"
            :disabled="isBtnDisabled"
            @click="addAttrDialogVisible = true"
            >添加参数</el-button
          >
          <!--参数展示表格-->
          <el-table :data="manyTableData" border stripe>
            <el-table-column type="expand">
              <template slot-scope="scope">
                <el-tag
                  type="success"
                  v-for="(item, i) in scope.row.attr_vals"
                  :key="i"
                  closable
                  @close="removeAttr(i, scope.row)"
                  >{{ item }}</el-tag
                >
                <el-input
                  class="input-new-tag"
                  v-if="scope.row.inputVisible"
                  v-model="scope.row.inputValue"
                  ref="saveTagInputRef"
                  size="small"
                  @keyup.enter.native="handleInputConfirm(scope.row)"
                  @blur="handleInputConfirm(scope.row)"
                >
                </el-input>
                <el-button
                  v-else
                  class="button-new-tag"
                  size="small"
                  @click="showInput(scope.row)"
                  >+ New Tag</el-button
                >
              </template>
            </el-table-column>
            <el-table-column type="index"></el-table-column>
            <el-table-column
              label="参数名称"
              prop="attr_name"
            ></el-table-column>
            <el-table-column label="操作">
              <template slot-scope="scope">
                <el-button
                  type="primary"
                  icon="el-icon-edit"
                  size="mini"
                  @click="showEditAttrDialog(scope.row.attr_id)"
                  >编辑</el-button
                >
                <el-button
                  type="danger"
                  icon="el-icon-delete"
                  size="mini"
                  @click="removeParams(scope.row.attr_id)"
                  >删除</el-button
                >
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
        <!--添加属性面板-->
        <el-tab-pane label="静态属性" name="only">
          <el-button
            type="primary"
            size="mini"
            :disabled="isBtnDisabled"
            @click="addAttrDialogVisible = true"
            >添加属性</el-button
          >
          <el-table :data="onlyTableData" border stripe>
            <el-table-column type="expand">
              <template slot-scope="scope">
                <el-tag
                  type="success"
                  v-for="(item, i) in scope.row.attr_vals"
                  :key="i"
                  closable
                  @close="removeAttr(i, scope.row)"
                  >{{ item }}</el-tag
                >
                <el-input
                  class="input-new-tag"
                  v-if="scope.row.inputVisible"
                  v-model="scope.row.inputValue"
                  ref="saveTagInputRef"
                  size="small"
                  @keyup.enter.native="handleInputConfirm(scope.row)"
                  @blur="handleInputConfirm(scope.row)"
                >
                </el-input>
                <el-button
                  v-else
                  class="button-new-tag"
                  size="small"
                  @click="showInput(scope.row)"
                  >+ New Tag</el-button
                >
              </template>
            </el-table-column>
            <el-table-column type="index"></el-table-column>
            <el-table-column
              label="参数名称"
              prop="attr_name"
            ></el-table-column>
            <el-table-column label="操作">
              <template slot-scope="scope">
                <el-button
                  type="primary"
                  icon="el-icon-edit"
                  size="mini"
                  @click="showEditAttrDialog(scope.row.attr_id)"
                  >编辑</el-button
                >
                <el-button
                  type="danger"
                  icon="el-icon-delete"
                  size="mini"
                  @click="removeParams(scope.row.attr_id)"
                  >删除</el-button
                >
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
      </el-tabs>
    </el-card>
    <!--添加参数对话框-->
    <el-dialog
      :title="'添加' + titleText"
      :visible.sync="addAttrDialogVisible"
      width="50%"
      @close="addAttrDialogClosed"
    >
      <el-form
        :model="addForm"
        :rules="addFormRules"
        ref="addFormRef"
        label-width="100px"
      >
        <el-form-item :label="titleText" prop="attr_name">
          <el-input v-model="addForm.attr_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addAttrDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="addAttr">确 定</el-button>
      </span>
    </el-dialog>
    <!--修改参数对话框-->
    <el-dialog
      :title="'修改' + titleText"
      :visible.sync="editAttrDialogVisible"
      width="50%"
      @close="editAttrDialogClosed"
    >
      <el-form
        :model="editForm"
        :rules="editFormRules"
        ref="editFormRef"
        label-width="100px"
      >
        <el-form-item :label="titleText" prop="attr_name">
          <el-input v-model="editForm.attr_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editAttrDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="editAttr">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      //商品分类列表
      cateList: [],
      //级联选择框已选中的商品分类id数组
      selectedKeys: [],
      //级联选择框的下拉框数据定义
      cateProps: {
        value: "cat_id",
        label: "cat_name",
        children: "children",
      },
      //标签页双向绑定的选中项
      activeName: "many",
      //保存动态参数列表的数据
      manyTableData: [],
      //保存静态属性列表的数据
      onlyTableData: [],
      //控制添加参数对话框是否显示
      addAttrDialogVisible: false,
      //添加参数表单
      addForm: {
        attr_name: "",
      },
      //添加参数表单验证规则
      addFormRules: {
        attr_name: [
          { required: true, message: "请输入活动名称", trigger: "blur" },
        ],
      },
      //修改参数表单
      editForm: {},
      //修改参数表单验证规则
      editFormRules: {
        attr_name: [
          { required: true, message: "请输入活动名称", trigger: "blur" },
        ],
      },
      //控制修改参数对话框是否可见
      editAttrDialogVisible: false,
    };
  },
  computed: {
    isBtnDisabled() {
      //若this.selectedKeys.length不等于3，则返回true，禁用按钮，
      //否则返回false，启用按钮
      if (this.selectedKeys.length !== 3) {
        return true;
      } else {
        return false;
      }
    },
    //给添加参数和添加属性一个计算属性来获得选中的最后一个分类的Id
    cateId() {
      //若this.selectedKeys.length等于3，则返回选中的最后一个Id
      if (this.selectedKeys.length === 3) {
        return this.selectedKeys[this.selectedKeys.length - 1];
      } else {
        return null;
      }
    },
    //给添加参数对话框一个标题计算属性
    titleText() {
      //判断当前触发的是哪个标签
      if (this.activeName === "many") {
        return "动态参数";
      } else {
        return "静态属性";
      }
    },
  },
  created() {
    this.getCateList();
  },
  methods: {
    async getParams() {
      if(this.selectedKeys.length !== 3){
        this.selectedKeys = []
        this.manyTableData = []
        this.onlyTableData = []
        return 
      }
      const { data: res } = await this.$http.get(
        `categories/${this.cateId}/attributes`,
        {
          params: { sel: this.activeName },
        }
      );
      //判断状态码是否等于200，如果不等于则提示一条错误信息
      if (res.meta.status !== 200) {
        return this.$Message({
          message: "请求参数列表失败！",
          type: "error",
        });
      }
      console.log(res.data);
      res.data.forEach((item) => {
        item.attr_vals = item.attr_vals ? item.attr_vals.split(" ") : [];
        (item.inputVisible = false), (item.inputValue = "");
      });
      //通过this.activeName的值来判断当前获取的是动态参数还是静态属性数据列表
      //如果this.activeName为many则获取的是动态参数列表，为only则获取的是静态属性数据列表
      //判断当前获取的是动态参数数据列表还是静态属性数据列表,
      //若是动态参数数据列表将获取到的数据列表保存到manyTableData
      //若是静态参数数据列表获取的是数据列表保存到onlyTableDate
      if (this.activeName === "many") {
        this.manyTableData = res.data;
      } else {
        this.onlyTableData = res.data;
      }
    },
    async getCateList() {
      const { data: res } = await this.$http.get("categories");
      if (res.meta.status !== 200) {
        return this.$Message({
          message: "获取商品分类失败！",
          type: "error",
        });
      }
      //将获取的商品分类列表保存到cateLits
      this.cateList = res.data;
    },
    //级联选择框发生改变时触发的函数
    async cateChange() {
      this.getParams();
    },
    //标签页变化时触发的函数
    handleClick() {
      if (this.selectedKeys.length === 3) {
        this.getParams();
      }
    },
    //当对话框关闭时触发的函数
    addAttrDialogClosed() {
      this.$refs.addFormRef.resetFields();
    },
    //添加参数函数
    addAttr() {
      //先验证表单数据是否正确
      this.$refs.addFormRef.validate(async (valid) => {
        if (!valid) return;
        //如果正确就发起一个添加参数的请求
        const { data: res } = await this.$http.post(
          `categories/${this.cateId}/attributes`,
          {
            attr_name: this.addForm.attr_name,
            attr_sel: this.activeName,
          }
        );
        //通过返回的状态码判断是否请求成功
        if (res.meta.status !== 201) {
         return this.$Message({
          message: "添加动态参数失败！",
          type: "error",
        });
        }
        //刷新参数列表
        this.getParams();
        //关闭对话框
        this.addAttrDialogVisible = false;
      });
    },
    //显示修改参数对话框函数
    async showEditAttrDialog(attr_id) {
      //发起获取参数信息的请求
      const { data: res } = await this.$http.get(
        `categories/${this.cateId}/attributes/${attr_id}`,
        { params: { attr_sel: this.activeName } }
      );
      //判断返回状态码是否为200，如果不是则退出
      if (res.meta.status !== 200) {
         return this.$Message({
          message: "获取参数信息失败！",
          type: "error",
        });
      }
      //并将获取到的参数信息保存到表单中editForm
      this.editForm = res.data;
      //打开对话框
      this.editAttrDialogVisible = true;
    },
    //监听对话框关闭时触发的函数
    editAttrDialogClosed() {
      this.$refs.editFormRef.resetFields();
    },
    editAttr() {
      //验证表单数据是否符合验证
      this.$refs.editFormRef.validate(async (valid) => {
        //判断valid是否为false，若为false则退出
        if (!valid) return;
        //若验证通过则发起修改参数请求
        const { data: res } = await this.$http.put(
          `categories/${this.cateId}/attributes/${this.editForm.attr_id}`,
          {
            attr_name: this.editForm.attr_name,
            attr_sel: this.editForm.attr_sel,
          }
        );
        //判断状态码是否等于200，如果不等于则修改失败
        if (res.meta.status !== 200) {
          return this.$Message({
          message: "修改数据失败！",
          type: "error",
        });
        }
        //刷新参数列表
        this.getParams();
        //关闭对话框
        this.editAttrDialogVisible = false;
      });
    },
    //删除参数函数
    async removeParams(attr_id) {
      //提示弹框
      const reslutcom = await this.$confirm(
        "此操作将永久删除该文件, 是否继续?",
        "提示",
        {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
        }
      ).catch((err) => err);
      if (reslutcom !== "confirm") {
         return this.$Message({
          message: "已取消删除！",
          type: "error",
        });
      }
      //发起删除参数请求
      const { data: res } = await this.$http.delete(
        `categories/${this.cateId}/attributes/${attr_id}`
      );
      //如果状态码值不等于200，则删除参数失败
      if (res.meta.status !== 200) {
         return this.$Message({
          message: "删除参数失败！",
          type: "error",
        });
      }
      //刷新参数列表
      this.getParams();
    },
    async handleInputConfirm(row) {
      if (row.inputValue.trim().length === 0) {
        //将输入框内容置为空
        row.inputValue = "";
        //将input框切换到按钮
        row.inputVisible = false;
        return;
      }
      //去除空格
      row.inputValue = row.inputValue.trim();
      //如果输入的内容不为空，则将inputValue添加到arrt_vals数组中
      row.attr_vals.push(row.inputValue);
      //再将变化的attr_vals添加到数据库中保存
      //调用参数提交请求
       this.saveAttr(row)
      //将输入框清空
      row.inputValue = "";
      //切换成按钮
      row.inputVisible = false;
    },
    //保存修改的参数提交请求的函数
    async saveAttr(row) {
      const { data: res } = await this.$http.put(
        `categories/${this.cateId}/attributes/${row.attr_id}`,
        {
          attr_name: row.attr_name,
          attr_sel: row.attr_sel,
          attr_vals: row.attr_vals.join(" "),
        }
      );
      if (res.meta.status !== 200) {
         return this.$Message({
          message: "提交参数失败！",
          type: "error",
        });
      }
    },
    showInput(row) {
      row.inputVisible = true;
      //当input元素渲染完成以后在调用自动获得焦点
      this.$nextTick((_) => {
        this.$refs.saveTagInputRef.$refs.input.focus();
      });
    },
    removeAttr(i, row) {
      //先将要删除的参数从attr.vals数组中移除
      row.attr_vals.splice(i, 1);
      //调用参数提交请求
       this.saveAttr(row)
    },
  },
};
</script>
<style lang="less" scoped>
.cat_top {
  margin: 15px 0;
}
.el-tag {
  margin: 10px;
}
.input-new-tag {
  width: 100px;
}
</style>

