<template>
  <div>
    <!--添加面包屑导航-->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>商品分类</el-breadcrumb-item>
    </el-breadcrumb>
    <!--卡片区域-->
    <el-card>
      <el-row>
        <el-col>
          <el-button type="primary" @click="showAddCateDialog"
            >添加分类</el-button
          >
        </el-col>
      </el-row>
      <!--table区域-->
      <tree-table
        class="treeTable"
        :data="cateList"
        :columns="columns"
        show-index
        border
        :selection-type="false"
        :expand-type="false"
        index-text="#"
        :show-row-hover="false"
      >
        <!--是否有效区域-->
        <template slot="isok" slot-scope="scope">
          <i
            class="el-icon-success"
            style="color: lightgreen"
            v-if="scope.row.cat_deleted === false"
          ></i>
          <i class="el-icon-error" style="color: red" v-else></i>
        </template>
        <!--排序区域-->
        <template slot="order" slot-scope="scope">
          <el-tag v-if="scope.row.cat_level === 0">一级</el-tag>
          <el-tag v-else-if="scope.row.cat_level === 1" type="success"
            >二级</el-tag
          >
          <el-tag v-else type="warning">三级</el-tag>
        </template>
        <!--操作区域-->
        <template slot="opt" slot-scope="scope">
          <el-button
            type="primary"
            icon="el-icon-edit"
            size="mini"
            @click="showEditDailog(scope.row.cat_id)"
            >编辑</el-button
          >
          <el-button
            type="danger"
            icon="el-icon-delete"
            size="mini"
            @click="deleteCateById(scope.row.cat_id)"
            >删除</el-button
          >
        </template>
      </tree-table>
      <!--分页区域-->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[3, 5, 10, 15]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>
    <!--添加分类对话框-->
    <el-dialog
      title="添加分类"
      :visible.sync="addCateDialogVisible"
      width="50%"
      @close="resetCateForm"
    >
      <el-form
        :model="cateForm"
        :rules="cateFormRules"
        ref="cateFormRef"
        label-width="100px"
      >
        <el-form-item label="分类名称" prop="cat_name">
          <el-input v-model="cateForm.cat_name"></el-input>
        </el-form-item>
        <el-form-item label="父级分类">
          <el-cascader
            class="Cascader"
            expand-trigger="hover"
            v-model="selectedKeys"
            :options="ParentCateList"
            :props="props"
            @change="cateFormChange"
            clearable
          ></el-cascader>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addCateDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="addCate">确 定</el-button>
      </span>
    </el-dialog>
    <!--修改分类对话框-->
    <el-dialog title="修改分类" :visible.sync="editDialogVisible"
     width="50%"
     @close='dialogClosed'
    >
      <el-form
        :model="editForm"
        :rules="editFormRules"
        ref="editFormRef"
        label-width="100px"
        
      >
        <el-form-item label="分类名称" prop="cateName">
          <el-input v-model="editForm.cateName"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="editCate()"
          >确 定</el-button
        >
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    return {
      //保存商品分类列表数据
      cateList: [],
      //请求商品分类列表的参数
      queryInfo: {
        type: 3,
        pagenum: 1,
        pagesize: 5,
      },
      //商品列表数据的总数
      total: 0,
      //表格列数据
      columns: [
        {
          label: "分类名称",
          prop: "cat_name",
        },
        {
          label: "是否有效",
          type: "template",
          template: "isok",
        },
        {
          label: "排序",
          type: "template",
          template: "order",
        },
        {
          label: "操作",
          type: "template",
          template: "opt",
        },
      ],
      //控制对话框是否可见
      addCateDialogVisible: false,
      cateForm: {
        cat_pid: 0,
        cat_name: "",

        cat_level: 0,
      },
      //表单校验规则
      cateFormRules: {
        cat_name: [
          { required: true, message: "请输入分类名称", trigger: "blur" },
        ],
      },
      //保存前两级的分类列表
      ParentCateList: [],
      props: {
        label: "cat_name",
        value: "cat_id",
        children: "children",
        checkStrictly: true,
      },
      selectedKeys: [],
      delCateDialogVisible: false,
      editDialogVisible: false,
      editForm:{
        cateName:'',
        cateId:0
      },
      editFormRules:{
           cateName:{ required: true, message: '请输入活动名称', trigger: 'blur' }
      }
    };
  },
  created() {
    this.getCateList();
  },
  methods: {
    async getCateList() {
      //发起获取商品列表数据的请求
      const { data: res } = await this.$http.get("categories", {
        params: this.queryInfo,
      });
      if (res.meta.status !== 200) {
        this.$Message.error("获取商品列表数据失败");
      }
      console.log(res.data);
      //将获取的商品数据列表存到cataList
      this.cateList = res.data.result;
      //将获取的商品数据总数存到total中
      this.total = res.data.total;
    },
    handleSizeChange(val) {
      //将最新的每页数量赋值给pagesize
      this.queryInfo.pagesize = val;
      //并重新刷新表格
      this.getCateList();
    },
    handleCurrentChange(val) {
      //将最新页码赋值给pagenum
      this.queryInfo.pagenum = val;
      //并重新刷新表格
      this.getCateList();
    },
    //显示添加分类对话框
    showAddCateDialog() {
      //
      this.getParentCateList();
      this.addCateDialogVisible = true;
    },
    //获取前两级商品分类数据
    async getParentCateList() {
      const { data: res } = await this.$http.get("categories", {
        params: { type: 2 },
      });

      if (res.meta.status !== 200) {
        return this.$Message.error("获取前两级商品分类数据失败");
      }

      //将获取到的数据保存到ParentCateList
      this.ParentCateList = res.data;
    },
    cateFormChange() {
      //判断被选中的id数组是否大于0,若大于0则将最后一级id赋值给addCateFrom.cat_id
      //若小于0,则addCateFrom.cat_id = 0，addCateFrom.cat_level = 0
      if (this.selectedKeys.length > 0) {
        this.cateForm.cat_pid = this.selectedKeys[this.selectedKeys.length - 1];
        this.cateForm.cat_level = this.selectedKeys.length;
        return;
      } else {
        this.cateForm.cat_pid = 0;
        this.cateForm.cat_level = 0;
      }
    },
    resetCateForm() {
      //将表单重置
      this.$refs.cateFormRef.resetFields();
      //并将selectedKeys设为空数组
      (this.selectedKeys = []),
        //将cat_id设置为0
        (this.cateForm.cat_pid = 0);
      //将cat_level设置为0
      this.cateForm.cat_level = 0;
    },
    //添加分类函数
    addCate() {
      //先验证输入的数据是否符合验证
      this.$refs.cateFormRef.validate(async (valid) => {
        //如果验证不通过就return出去
        if (!valid) return;

        //发起post请求将分类添加到分类数据列表
        const { data: res } = await this.$http.post(
          "categories",
          this.cateForm
        );
        if (res.meta.status !== 201) {
          return this.$Message.error("添加分类失败");
        }
        console.log(res.data);
        //添加成功便刷新分类列表
        this.getCateList();
        //将对话框关闭
        this.addCateDialogVisible = false;
      });
    },
    async deleteCateById(id) {
      const result = await this.$confirm(
        "此操作将永久删除该文件, 是否继续?",
        "提示",
        {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
        }
      ).catch((err) => err);
      if (result !== "confirm") {
        return this.$Message.info("已经取消了删除");
      }
      console.log(id);
      const { data: res } = await this.$http.delete(`categories/${id}`);
      if (res.meta.status !== 200) {
        return this.$Message.error("删除商品失败");
      }
      //删除成功则刷新商品分类列表
      this.getCateList();
    },
    async showEditDailog(id) {
      const{data:res} = await this.$http.get(`categories/${id}`)
      if(res.meta.status !== 200){
        return this.$Message.error('查询商品信息失败')
      }
      this.editForm.cateId = res.data.cat_id
      
      this.editDialogVisible = true;
    },
    dialogClosed(){
      this.$refs.editFormRef.resetFields()
    },
    async editCate(){
      const{data:res} = await this.$http.put(`categories/${this.editForm.cateId}`,{
        cat_name: this.editForm.cateName
      })
      if(res.meta.status !== 200){
        return this.$Message.error('修改失败')
      }
      this.getCateList()
      this.editDialogVisible = false 
    }
  },
};
</script>
<style lang="less" scoped>
.treeTable {
  margin-top: 15px;
}
.Cascader {
  width: 100%;
}
</style>
